.navbar {
    height: var(--navbar-height);
    width: 100%;
    border-bottom: 1px solid var(--border-primary-color);
    display: flex;
    align-items: center;
    padding-left: var(--padding-md);
    box-sizing: border-box;

    &__logo{
        padding-right: var(--padding-lg);

        &-text{
            margin:0;
            font-weight: var(--typo-weight-md);
            font-size: var(--typo-lg);
        }
    }

    &__title{
        padding-left: var(--padding-lg);
        
        &-text{
            margin:0;
            font-weight: 500;
            font-size: var(--typo-lg);
        }
    }

    &__navigation {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top:0.4rem;
        padding-left: var(--padding-md);
        height: 100%;
        box-sizing: border-box;
        gap: var(--padding-sm);

        &-button{
            background: none;
            border: none;
            outline: none;
            cursor: pointer;
            width: 1.75rem;
        }
    }
}

@media screen and (max-width: 800px) {
    .navbar {    
        &__logo{
            display: none;
        }

        &__navigation {
            padding-left: 0rem;
        }
    }
}