.actionbar{
    width: 18rem;
    min-width: 18rem;
    height: 100%;
    border-right: 1px solid var(--border-primary-color);
    padding: var(--padding-md);
    box-sizing: border-box;
}

@media screen and (max-width: 1400px) {
    .actionbar{
        width: 14rem;
        min-width: 14rem;
    }
}

@media screen and (max-width: 1000px) {
    .actionbar{
        width: 12rem;
        min-width: 12rem;
    }
}

@media screen and (max-width: 800px) {
    .actionbar{
        display: none;
    }
}