.modal {
    &-content {
        width: 100%;
        height: 100%;

        &__title {
            display: flex;

            &-text {
                font-weight: var(--typo-weight-lg);
                font-size: var(--typo-md-50);
                width: 100%;
                word-wrap: break-word;
            }
        }

        &__description {
            display: flex;
            padding-top: var(--padding-sm);
            padding-bottom: var(--padding-md);
            overflow-wrap: anywhere;
            
            &-text {
                font-weight: var(--typo-weight-md);
                font-size: var(--typo-md);
            }
        }

        &__payment {
            &-text{
                font-weight: var(--typo-weight-lg);
                color: green;
                font-size: var(--typo-md);
            }
        }

        &__day {
            display: flex;
            padding-top: var(--padding-lg);
            overflow-wrap: anywhere;
            gap: var(--padding-sm);
            
            &-text {
                font-weight: var(--typo-weight-md);
                font-size: var(--typo-md);
            }
        }
    }
}

@media screen and (max-width: 800px) {
    .modal {
        top: 2.5%;
        left: 2.5%;
        position: fixed;
        width: 95%;
        height: 95%;
        opacity: 1;
        box-shadow: 0px 0px 0px 1000px rgba(0,0,0,0.7);
    }
    .modal-wrapper {
        width: 100%;
    }
}