#root {
    // THEME
    --primary-color : rgba(99,115,240,1);
    --primary-color-50: rgba(99,115,240,0.5);
    --primary-color-30: rgba(99,115,240,0.3);

    --secondary-color : rgba(79,95,220,255);

    --ghost-color-10: rgba(0,0,0,0.1);
    --ghost-color-20: rgba(0,0,0,0.2);

    --success-hover-color: rgb(71, 191, 83);
    --success-color: rgba(105, 216, 116, 1);

    // BORDERS
    --border-primary-color: rgba(0,0,0,0.2);

    --border-radius-sm: 0.2rem;
    --border-radius-md: 0.5rem;

    // SPACING
    --padding-sm: 0.5rem;
    --padding-md: 1rem;
    --padding-lg: 2rem;

    // TYPOGRAPHY
    --typo-sm: 0.75rem;
    --typo-md: 1rem;
    --typo-md-50:1.25rem;
    --typo-lg: 1.5rem;

    --typo-weight-sm: 200;
    --typo-weight-md: 400;
    --typo-weight-lg: 500;

    --typo-primary-color:rgb(60,64,67);

    // OTHERS
    --navbar-height: 4rem;
}